.addhspbtn { margin-right:10px; background-color:#5d36b1; color:#fff;}
.purple-btn { background-color:#5d36b1; color:#fff; }


/**************
Login Page 
***************/

.alertMsg { 

	position: absolute; top:0;  right:5px; opacity:0; z-index:9999;

.alert { padding:.25em .5em;}

p { display:inline-block; margin-right: 11px; padding-top: 16px;}

}


.showAlert { -webkit-animation:fadeInOut 3s ease-out;-o-animation:fadeInOut 2.5s ease-out;-moz-animation:fadeInOut 2.5s ease-out;-ms-animation:fadeInOut 2.5s ease-out; animation:fadeInOut 2.5s ease-out; }

@-webkit-keyframes fadeInOut {
    
    0% { opacity:1;}
    65%{ opacity:0.5;}
	100% { opacity:0;}
}

@keyframes fadeInOut {
    
    0% {  opacity:1;}
    65%{  opacity:0.5;}
	100% {  opacity:0;}
}


